
/*--Общие стили--*/
* html, body {
  margin: 0;
  padding: 0;
}
body {
  background: #e3e0d5;
  font-family: 'Roboto', sans-serif;
}
header,
footer,
main,
nav{
  display: block;
}
.flex {
  display: flex;
}
.c-align {
  align-items: center;

}
.s-between {
  justify-content: space-between;
}

.navbar {
  margin-bottom: 0;
}
@media (min-width: 1200px){
  .large-container {
    width: 100%;
    max-width: 1400px;
  }
}
/*--Конец Общие стили--*/


.icon-bar {
  background: red;
}
